import React from 'react'
import { Card, CardBody } from 'reactstrap'
import themeConfig from '../../configs/themeConfig'

class DashboardCards extends React.Component {
  render() {
    return (
      <Card className="whole-card">
        <CardBody className="p-0 m-0 row">
          <div
            className="col card_image"
            style={{
              backgroundImage: `url(${this.props.background})`
              // backgroundColor:
              //   this.props.stat === 'Nominees' ? '#95d6dc' : '#ffffff'
            }}
          />
          <div className="col h-100">
            <div className="row m-0">
              <h2
                style={{
                  color: themeConfig.theme === 'dark' ? 'white' : 'black'
                }}
                className=" mt-1 card_title"
              >
                {this.props.stat}
              </h2>
            </div>
            <div className="row mt-50">
              <ul style={{ padding: '0px 10px 0px 35px' }}>
                <li
                  style={{
                    color: themeConfig.theme === 'dark' ? 'white' : 'black'
                  }}
                  className="card_description"
                >
                  {this.props.statTitle}
                </li>
              </ul>
            </div>
          </div>
        </CardBody>
      </Card>
    )
  }
}
export default DashboardCards
