import React from 'react'
import { Row, Col } from 'reactstrap'
import Banner from './Banner'
import Assets from '../../assets/img/cards/Assets/1.jpg'
import Diary from '../../assets/img/cards/Diary/1.jpg'
import Documents from '../../assets/img/cards/Documents/1.jpg'
import Liability from '../../assets/img/cards/Liability/1.jpg'
import Nominee from '../../assets/img/cards/Nominee/1.jpg'
import Portfolio from '../../assets/img/cards/Portfolio/1.jpg'
import Sender from '../../assets/img/cards/Sender/1.jpg'
import Spend from '../../assets/img/cards/Spend/1.jpg'
import Vault from '../../assets/img/cards/Vault/1.jpg'
import 'swiper/css/swiper.css'
import {
  Users,
  Briefcase,
  Share,
  FileMinus,
  Command,
  DollarSign,
  Lock,
  Archive,
  FileText
} from 'react-feather'
import '../../assets/scss/plugins/extensions/swiper.scss'

import '../../assets/scss/plugins/charts/apex-charts.scss'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { ToastContainer } from 'react-toastify'
import '../../assets/scss/plugins/extensions/toastr.scss'
import themeConfig from '../../configs/themeConfig'
import DashboardCards from './DashboardCards'

import { connect } from 'react-redux'
import { changeLanguage } from '../../redux/actions/customizer'

const mobileColor = '#ff7d00'
const desktopColor = themeConfig.theme === 'dark' ? '#757488' : 'white'

class LADashboard extends React.Component {
  state = {
    width: window.innerWidth,
    messages: this.props.messages,
    columns: [
      {
        label: this.props.messages?.spends
          ? this.props.messages?.spends
          : 'Daily Spends',
        icon: <FileMinus color={mobileColor} className="danger" size="auto" />,
        path: '/spends'
      },
      {
        label: this.props.messages?.vault
          ? this.props.messages?.vault
          : 'Vault',
        icon: <Lock color={mobileColor} className="warning" size="auto" />,
        path: '/vault/password'
      },
      {
        label: this.props.messages?.diary
          ? this.props.messages?.diary
          : 'Secret Diary',
        icon: <FileText color={mobileColor} className="success" size="auto" />,
        path: '/diary'
      },
      {
        label: this.props.messages?.asset
          ? this.props.messages?.asset
          : 'Assets',
        icon: (
          <DollarSign color={mobileColor} className="primary" size="auto" />
        ),
        path: '/assets/add'
      },
      {
        label: this.props.messages?.liability
          ? this.props.messages?.liability
          : 'Liabilties',
        icon: <Command color={mobileColor} className="danger" size="auto" />,
        path: '/liability/add'
      },
      {
        label: this.props.messages?.nominees
          ? this.props.messages?.nominees
          : 'Nominees',
        icon: <Users color={mobileColor} className="warning" size="auto" />,
        path: '/nominee/list'
      },
      {
        label: this.props.messages?.documents
          ? this.props.messages?.documents
          : 'Documents',
        icon: <Archive color={mobileColor} className="primary" size="auto" />,
        path: '/vault/document'
      },
      {
        label: this.props.messages?.portfolio
          ? this.props.messages?.portfolio
          : 'Portfolio',
        icon: <Briefcase color={mobileColor} className="success" size="auto" />,
        path: '/portfolio'
      },
      {
        label: this.props?.sender?.heading
          ? this.props?.sender?.heading
          : 'Sender',
        icon: <Share color={mobileColor} className="success" size="auto" />,
        path: '/contactUs'
      }
    ],
    dashboard: [
      {
        label: this.props.messages?.asset
          ? this.props.messages?.asset
          : 'Assets',
        description: this.props.messages?.assetText
          ? this.props.messages?.assetText
          : 'Bank details, FD, RD, Insurance, Properties etc.',
        background: Assets,
        icon: (
          <DollarSign color={desktopColor} className="primary" size="auto" />
        ),
        path: '/assets/add'
      },
      {
        label: this.props.messages?.liability
          ? this.props.messages?.liability
          : 'Liabilties',
        description: this.props.messages?.liabilityText
          ? this.props.messages?.liabilityText
          : 'Loan, EMI, Cash borrowed etc with collateral details',
        background: Liability,
        icon: <Command color={desktopColor} className="danger" size="auto" />,
        path: '/liability/add'
      },
      {
        label: this.props.messages?.vault
          ? this.props.messages?.vault
          : 'Vault',
        description: this.props.messages?.vaultText
          ? this.props.messages?.vaultText
          : 'Save your passwords like cards, credentials in vault.',
        background: Vault,
        icon: <Lock color={desktopColor} className="warning" size="auto" />,
        path: '/vault/password'
      },
      {
        label: this.props.messages?.nominees
          ? this.props.messages?.nominees
          : 'Nominees',
        description: this.props.messages?.nomineesText
          ? this.props.messages?.nomineesText
          : 'Manage your nominees for your assets & liabities',
        background: Nominee,
        icon: <Users color={desktopColor} className="warning" size="auto" />,
        path: '/nominee/list'
      },
      {
        label: this.props.messages?.spends
          ? this.props.messages?.spends
          : 'Daily Spends',
        description: this.props.messages?.spendsText
          ? this.props.messages?.spendsText
          : 'Log your thoughts and analyze your daily spends.',
        background: Spend,
        icon: <FileMinus color={desktopColor} className="danger" size="auto" />,
        path: '/spends'
      },
      {
        label: this.props.messages?.documents
          ? this.props.messages?.documents
          : 'Documents',
        description: this.props.messages?.documentsText
          ? this.props.messages?.documentsText
          : 'Upload you documents, pics in your own secret vault',
        background: Documents,
        icon: <Archive color={desktopColor} className="primary" size="auto" />,
        path: '/vault/document'
      },
      {
        label: this.props.messages?.diary
          ? this.props.messages?.diary
          : 'Secret Diary',
        description: this.props.messages?.diaryText
          ? this.props.messages?.diaryText
          : 'Log your thoughts and secrets in a personal space',
        background: Diary,
        icon: <FileText color={desktopColor} className="success" size="auto" />,
        path: '/diary'
      },
      {
        label: this.props.messages?.portfolio
          ? this.props.messages?.portfolio
          : 'Portfolio',
        description: this.props.messages?.portfolioText
          ? this.props.messages?.portfolioText
          : 'View & analyze portfolio of saved assets and liabilties',
        background: Portfolio,
        icon: (
          <Briefcase color={desktopColor} className="success" size="auto" />
        ),
        path: '/portfolio'
      },
      {
        label: this.props?.sender?.heading
          ? this.props?.sender?.heading
          : 'Sender',
        description: this.props?.sender?.subHeading
          ? this.props?.sender?.subHeading
          : 'Save, schedule, send text, audio, video messages',
        background: Sender,
        icon: <Share color={desktopColor} className="success" size="auto" />,
        path: '/contactUs'
      }
    ]
  }
  componentDidMount() {
    // this.props.changeLanguage('english')
    if (localStorage.getItem('logInGreeting')) {
      toast.success('Good to see you back !!!!!!!!')
      //toast.error('Abhi tak mare nhi');
      localStorage.removeItem('logInGreeting')
    }
    window.addEventListener('resize', this.updateWidthAndHeight)
    return () => window.removeEventListener('resize', this.updateWidthAndHeight)
  }
  updateWidthAndHeight = () => {
    this.setState({ width: window.innerWidth })
  }

  calcWidth = () => {
    if (this.state.width > 1100) {
      return '4'
    } else if (this.state.width > 900 && this.state.width <= 1100) {
      return '6'
    } else {
      return '12'
    }
  }

  render() {
    return (
      <div className="mobile_content">
        <ToastContainer />

        <div
          style={{
            width: '100%',
            marginLeft: this.state.width <= 500 ? '0px' : '-2px'
          }}
        >
          <Banner />
        </div>
        {this.state.width <= 500 && (
          <div className="menu_content">
            {this.state.columns.map((item, index) => {
              return (
                <div
                  className="menu_options"
                  key={index}
                  onClick={() => {
                    this.props.history.push(item.path)
                  }}
                  style={{
                    borderLeft:
                      (index === 0 || index === 3 || index === 6) && '0px',
                    borderTop:
                      (index === 0 || index === 1 || index === 2) && '0px',
                    boxShadow:
                      themeConfig.theme === 'dark'
                        ? '0px 0px 19px -10px #ff7d00'
                        : '0px 0px 25px -10px #ff7d00'
                  }}
                >
                  <div className="menu_icon">{item.icon}</div>
                  <label className="menu_label">{item.label}</label>
                </div>
              )
            })}
          </div>
        )}
        {this.state.width > 500 && (
          <>
            <Row className="mt-2 px-2">
              {this.state.dashboard.map((item, index) => {
                return (
                  <Col md={this.calcWidth()} sm="12" key={index}>
                    <div
                      onClick={() => {
                        this.props.history.push(item.path)
                      }}
                      className="pointerCursor"
                    >
                      <DashboardCards
                        size={this.state.width}
                        icon={item.icon}
                        background={item.background}
                        iconBg={desktopColor}
                        stat={item.label}
                        statTitle={item.description}
                        hideChart={true}
                      />
                    </div>
                  </Col>
                )
              })}
            </Row>
          </>
        )}
      </div>
    )
  }
}
const mapStateToProps = (state) => {
  const { customizer } = state?.customizer
  return {
    messages: customizer?.language?.Dashboard,
    sender: customizer?.language?.Sender
  }
}

export default connect(mapStateToProps, { changeLanguage })(LADashboard)
